<script>
import eggWinners from '../../static/eggWinner'
import shares from '../../static/share'
import node from '../../static/node'
import { urlFilter } from '../Mixin/urlFilter'
import ethUtils from '../utils/ethUtils'
import langEn from '../utils/lang/en-us'
import langZh from '../utils/lang/zh-cn'
import store from '../store/index'
export default {
  name: 'Statistics',
  mixins: [urlFilter],
  data () {
    return {
      terminatorBlockNumId: '',
      accountBlockNumId: '',
      straightAddressLen: 0, // straightInviteAddress[] 的长度
      yourTeam: {
        tableData: [],
        // 总共多少数目
        total: 0,
        // 每页多少数目
        pagesize: 10,
        // 当前页面
        currentPage: 1
      },
      share: {
        tableData: [],
        total: 0,
        pagesize: 10,
        currentPage: 1
      },
      eggWinner: {
        tableData: [],
        total: 0,
        pagesize: 10,
        currentPage: 1
      },
      bonusRankData: '', // 分红
      easterWinnerData: '', // 彩蛋
      terminatorWinners: '',
      leftinfo: [
        { imgurl: require('../assets/img/holding.png'),
          title: this.$t('statistics.holding'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/dividends.png'),
          title: this.$t('statistics.dividends'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/invites.png'),
          title: this.$t('statistics.invites'),
          amount: '0',
          unit: this.$tc('commom.unit', 1)
        }, {
          imgurl: require('../assets/img/level.png'),
          title: this.$t('statistics.level'),
          amount: '0',
          unit: this.$tc('commom.unit', 2)
        }, {
          imgurl: require('../assets/img/afterfounds.png'),
          title: this.$t('statistics.afterFounds'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/noderwards.png'),
          title: this.$t('statistics.nodeRwards'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/referralrwards.png'),
          title: this.$t('statistics.referralRwards'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/teamrwards.png'),
          title: this.$t('statistics.teamRwards'),
          amount: '0'
        }
      ],
      rightinfo: [
        { imgurl: require('../assets/img/afterfounds.png'),
          title: this.$t('statistics.outOfBalance'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/referralrwards.png'),
          title: this.$t('statistics.referralRwardsTotal'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/teamrwards.png'),
          title: this.$t('statistics.teamRwardsTotal'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/dividends.png'),
          title: this.$t('statistics.totalDividends'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/noderwards.png'),
          title: this.$t('statistics.totalRevenue'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/referralrwards.png'),
          title: this.$t('statistics.totalInvestment'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/noderwards.png'),
          title: this.$t('statistics.nodeRwardsTotal'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/dividends.png'),
          title: this.$t('statistics.drawnDividends'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/teamrwards.png'),
          title: this.$t('statistics.totalTeamPerformance'),
          amount: '0'
        }
      ],
      contract: [
        { imgurl: require('../assets/img/ranking.png'),
          title: this.$t('statistics.recommendedRankingPool'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/terminator.png'),
          title: this.$t('statistics.terminatorPool'),
          amount: '0'
        }, {
          imgurl: require('../assets/img/egg.png'),
          title: this.$t('statistics.eggPool'),
          amount: '0',
          icon: 'el-icon-right'
        }, {
          imgurl: require('../assets/img/dividend.png'),
          title: this.$t('statistics.globalBonus'),
          amount: '0',
          icon: 'el-icon-right'
        }, {
          imgurl: require('../assets/img/dividend.png'),
          title: this.$t('statistics.Keydividend'),
          amount: '0',
          icon: 'el-icon-right'
        }
      ],
      leftinfoTitle: [],
      rightinfoTitle: [],
      contractTitle: [],
      totalTeamPerformance: '' // todo 8.12 团队总业绩，从静态JSON资源中获取
    }
  },
  computed: {
    teamPerformance () {
      return this.totalTeamPerformance
    },
    terminatorBlockNumInfo () {
      return this.$store.state.contract.terminatorBlockNumInfo
    },
    // 直推区块号结算
    accountBlockNumInfo () {
      return this.$store.state.contract.accountBlockNumInfo
    },
    personalData () { // 个人数据统计 todo 页面已注入
      const per = this.$store.state.contract.personalStatistics
      const personalLeft = [per.holdings, per.dividends, per.invites, per.level, per.afterFounds, per.nodeRewards] // len 6
      let outDiff = '--'
      if (per.holdings !== undefined) {
        outDiff = parseFloat(per.holdings) * 1.2 - parseFloat(per.dividends)
        outDiff = ethUtils.numFloatLen(outDiff, 6) // 需求改动
        if (outDiff < 0) {
          outDiff = 0.0000
        }
      }
      const personalRight = [outDiff, per.referralRewards, per.teamRewards] // len 3
      return {
        personalLeft,
        personalRight
      }
    },
    currentRwards () { // 当前奖励，userbalance中获取 teamReward和recommendReward
      let userBalance = this.$store.state.contract.userBalance
      if (userBalance) {
        return [userBalance.recommendBalance4, userBalance.teamBalance4]
      } else {
        return ['--', '--']
      }
    },
    nodeBonusTotal () {
      let nodeBonus = this.$store.state.contract.whiteListNodeBonus
      let userBalance = this.$store.state.contract.userBalance
      let staticBalance = '--'
      let totalInvest = '--'
      let totalDivided = '--'
      let withdrawDivided = '--'
      if (nodeBonus === null) {
        nodeBonus = '--'
      }
      if (userBalance) {
        staticBalance = userBalance.staticBalance4
        totalInvest = userBalance.totalInvest // 总投入
        totalDivided = userBalance.totalDivided // 总分红
        withdrawDivided = userBalance.withdrawDivided // 已提总分红
      }
      return {
        nodeBonus,
        staticBalance,
        totalInvest,
        totalDivided,
        withdrawDivided
      }
    },
    commitMark: function () {
      return this.$store.state.metamask.commitMark
    },
    straightInviteAddress () {
      return this.$store.state.contract.straightInviteAddress
    },
    teamData () {
      return this.$store.state.contract.teamData
    },
    contractData () {
      const contract = this.$store.state.contract.contractStatistics
      const contractArr = [contract.recommendRankPool, contract.terminatorPool, contract.eggPool, contract.globalDividendPool, contract.nodePool] // todo 新增globalDividendPool，全球分红奖池8.08
      return contractArr
    },
    listRankData () {
      let recommendRankPool = this.$store.state.contract.contractStatistics.recommendRankPool
      const listRank = this.$store.state.contract.rankOfRecommend
      const rankData = []
      if (recommendRankPool && listRank.length > 0) {
        recommendRankPool = parseFloat(recommendRankPool) / 2 // todo 8.09 修改，分享直推奖池中一半的额度
        recommendRankPool = ethUtils.numFloatLen(recommendRankPool, 5)
        let sum = 0 // 总人数
        for (let i = 0; i < listRank.length; i++) {
          sum += listRank[i].inviteNumber
        }
        for (let j = 0; j < listRank.length; j++) {
          const rank = {}
          let eth = (listRank[j].inviteNumber / sum) * recommendRankPool
          rank.straightSortAddress = listRank[j].straightSortAddress
          rank.inviteNumber = listRank[j].inviteNumber
          rank.eth = ethUtils.numFloatLen(eth, 4)
          rankData[j] = rank
        }
      }
      return rankData
    },
    // 终结者数据处理
    terminatorInfo () {
      return this.$store.state.contract.terminatorInfo
    },
    achieveTerminator () {
      return this.$store.state.contract.achieveTerminator
    },
    whiteStatus () {
      const status = this.$store.state.metamask.whiteStatus
      if (status) {
        return true
      } else {
        return false
      }
    }
  },
  watch: {
    '$i18n.locale': {
      handler: function (newVal) {
        const statisticsEn = langEn.statistics
        const statisticsZh = langZh.statistics
        if (newVal === 'en') {
          this.leftinfoTitle = [statisticsEn.holding, statisticsEn.dividends, statisticsEn.invites, statisticsEn.level, statisticsEn.afterFounds, statisticsEn.nodeRwards, statisticsEn.referralRwards, statisticsEn.teamRwards]
          this.rightinfoTitle = [statisticsEn.outOfBalance, statisticsEn.referralRwardsTotal, statisticsEn.teamRwardsTotal, statisticsEn.totalDividends, statisticsEn.totalRevenue, statisticsEn.totalInvestment, statisticsEn.nodeRwardsTotal, statisticsEn.drawnDividends, statisticsEn.totalTeamPerformance]
          this.contractTitle = [statisticsEn.recommendedRankingPool, statisticsEn.terminatorPool, statisticsEn.eggPool, statisticsEn.globalBonus, statisticsEn.Keydividend]
        } else {
          this.leftinfoTitle = [statisticsZh.holding, statisticsZh.dividends, statisticsZh.invites, statisticsZh.level, statisticsZh.afterFounds, statisticsZh.nodeRwards, statisticsZh.referralRwards, statisticsZh.teamRwards]
          this.rightinfoTitle = [statisticsZh.outOfBalance, statisticsZh.referralRwardsTotal, statisticsZh.teamRwardsTotal, statisticsZh.totalDividends, statisticsZh.totalRevenue, statisticsZh.totalInvestment, statisticsZh.nodeRwardsTotal, statisticsZh.drawnDividends, statisticsZh.totalTeamPerformance]
          this.contractTitle = [statisticsZh.recommendedRankingPool, statisticsZh.terminatorPool, statisticsZh.eggPool, statisticsZh.globalBonus, statisticsZh.Keydividend]
        }
      },
      immediate: true
    },
    commitMark: function (newVal) {
      const status = this.$store.state.metamask.status
      if (status === 1) {
        this.$store.dispatch('contract/getPersonalStatistics')
        this.$store.dispatch('contract/getStraightInviteAddress')
        this.$store.dispatch('contract/getContractStatistics')
        this.$store.dispatch('contract/listRankOfRecommend')
        // 配合订阅，终结者没有必要每次进入页面都获取一边数据
        if (this.$store.state.contract.terminatorInfo.length === 0) {
          this.$store.dispatch('contract/getLatestTerminatorInfo')
        }
        this.$store.dispatch('contract/getUserBalance') // 为了获得本次的recommend和team奖励
        // 获取最近一次直推结算区块号
        this.$store.dispatch('contract/getContractBlockNum')
        this.accountBlockNumId = setInterval(function () {
          store.dispatch('contract/getContractBlockNum')
        }, 15000)
        // 获取终结者产生的相关信息
        this.$store.dispatch('contract/getTerminatorInfo')
        this.terminatorBlockNumId = setInterval(function () {
          store.dispatch('contract/getTerminatorInfo')
        }, 15000)
        // 获取当前账号地址对应的团队总业绩(teamPerformance),通过当前的用户地址遍历node.json文件中信息;todo 异步处理
        const currentAddress = this.$store.state.metamask.currentAccount
        if (currentAddress !== '') {
          this.getTeamPerformance(currentAddress).then(val => {
            if (val === null) {
              val = '0.0000'
            }
            this.totalTeamPerformance = val
          })
        }
      }
    },
    straightInviteAddress (newVal) { // 获取了所有的下级直推地址;每次进入statistics获取所有下级直推地址，并进行比对
      if (newVal.length > 0) { // 7/26 这部分的逻辑最好是放在getStraightInviteAddress接口的回调函数中处理，不够既然已经写好了，就不修改了
        // todo 获取了多少数据就展示多少页，由实际拿到的数据决定; 如果有新的数据更新，则是添加在数组末尾
        // todo 新增一个审核，需要比对账户地址是否更改，如果更改，则将teamData数据至空
        let addressLen = newVal.length
        let teamDataLen = this.$store.state.contract.teamDataLen
        const teamDataAccount = this.$store.state.contract.teamDataAccount
        const currentAccount = this.$store.state.metamask.currentAccount
        if (teamDataAccount !== currentAccount) {
          teamDataLen = 0
          this.$store.commit('contract/SET_TEAMDATACCOUNT', currentAccount)
          // 清空之前保存的teamData数据
          this.$store.commit('contract/SET_TEAMDATA', [])
        }
        this.$store.commit('contract/SET_TEAMDATALEN', addressLen)
        if (teamDataLen !== addressLen) {
          // 记录已经获取的直推的地址数量
          // 获取了新的直推地址，从已有的地址开始下一位开始
          const userAddressArr = newVal.slice(teamDataLen) // 获取所有未获取数据的直推地址
          this.$store.dispatch('contract/getUserStatistics', userAddressArr)
        }
      }
    },
    achieveTerminator (newVal) {
      if (newVal) {
        // 产生新的终结者, 更新终结者数据
        this.$store.dispatch('contract/getLatestTerminatorInfo')
        // 还原achieveTerminator状态
        this.$store.commit('contract/SET_ACHIEVETERMINATOR', false)
      }
    }
  },
  created () {
    this.init()
    // 开启终结者订阅
    const myProvider = this.$myProvider
    if (!this.$ethConf.subscribeMark.AchieveTerminator && myProvider) {
      if (myProvider.networkVersion === this.$ethConf.networkVersion) {
        const contractTerm = this.$myContracts && this.$myContracts.contractTerm
        if (contractTerm) {
          // 组件切换可能会重复订阅。。。 todo 到时测一下  可以使用全局标志，解决
          this.$store.dispatch('contract/startListenEvent', { contract: contractTerm, eventName: 'AchieveTerminator' })
        }
      }
    }
    // 开启数据获取
    const status = this.$store.state.metamask.status // 获取状态在后置导航守卫，可能会出现状态还没获取就进入了路由;解决：全局监听
    if (status === 1) { // 没有对应的订阅配合，所以每次进入路由都需要获取最新数据
      this.$store.dispatch('contract/getPersonalStatistics')
      this.$store.dispatch('contract/getStraightInviteAddress')
      this.$store.dispatch('contract/getContractStatistics')
      this.$store.dispatch('contract/listRankOfRecommend')
      // 配合订阅，终结者没有必要每次进入页面都获取一边数据
      if (this.$store.state.contract.terminatorInfo.length === 0) {
        this.$store.dispatch('contract/getLatestTerminatorInfo')
      }
      // 如果当前的用户是白名单，则获取whiteListNodeBonus数据;重新加载异步不会调用，不过在metamask中有调用
      if (this.$store.state.metamask.whiteStatus) {
        this.$store.dispatch('contract/whiteListNodeBonus')
      }
      this.$store.dispatch('contract/getUserBalance') // 为了获得本次的recommend和team奖励
      // 获取最近一次直推结算区块号
      this.$store.dispatch('contract/getContractBlockNum')
      this.accountBlockNumId = setInterval(function () {
        store.dispatch('contract/getContractBlockNum')
      }, 15000)
      // 获取终结者产生的相关信息
      this.$store.dispatch('contract/getTerminatorInfo')
      this.terminatorBlockNumId = setInterval(function () {
        store.dispatch('contract/getTerminatorInfo')
      }, 15000)
      // 获取当前账号地址对应的团队总业绩(teamPerformance),通过当前的用户地址遍历node.json文件中信息;todo 异步处理
      const currentAddress = this.$store.state.metamask.currentAccount
      if (currentAddress !== '') {
        this.getTeamPerformance(currentAddress).then(val => {
          if (val === null) {
            val = '0.0000'
          }
          this.totalTeamPerformance = val
        })
      }
    }
  },
  methods: {
    // 遍历node.json中的数据
    async getTeamPerformance (accountAddress) {
      const staticNodeData = node
      let teamPerformanceTotal = null
      staticNodeData.forEach(el => {
        if (el.nodeAddress === accountAddress) { // eth 账号不区分大小写，metamask插件操作，复制到剪贴板的账号地址是大写的
          teamPerformanceTotal = ethUtils.weiToEth(el.performance)
          teamPerformanceTotal = ethUtils.numFloatLen(teamPerformanceTotal, 6) // 个人板块，显示4位精度
        }
      })
      return teamPerformanceTotal
    },
    rankJumpLink (row) { // ranking of recommend直推排名，点击跳转到对应的区块链浏览器
      window.location.href = 'https://etherscan.io/address/' + row.straightSortAddress
    },
    jumpLink (index) { // contract最后三个点击跳转到对应的区块链浏览器；egg、global、node
      const linkArr = [this.$ethConf.eggPoolAddress, this.$ethConf.globalDividendAddress, this.$ethConf.nodePoolAddress]
      if (index >= 2) {
        window.location.href = 'https://etherscan.io/address/' + linkArr[index - 2]
      }
    },
    rightInfoMeth (index) {
      if (index < 3) {
        let personalInfo = this.personalData.personalRight[index]
        if (personalInfo === undefined) {
          personalInfo = '--'
        }
        return personalInfo
      } else if (index === 3) { // 第四个元素, 总分红
        return this.nodeBonusTotal.totalDivided
      } else if (index === 4) { // 总收入
        let referralRewardsTotal = this.personalData.personalRight[1]
        let teamRewardsTotal = this.personalData.personalRight[2]
        let bonusTotal = this.nodeBonusTotal.totalDivided
        if (referralRewardsTotal !== undefined && bonusTotal !== '--') {
          let rewardsTotal = parseFloat(referralRewardsTotal) + parseFloat(teamRewardsTotal) + parseFloat(bonusTotal)
          rewardsTotal = ethUtils.numFloatLen(rewardsTotal, 6) // 需求改动
          return rewardsTotal
        } else {
          return '--'
        }
      } else if (index === 5) { // 总投入
        return this.nodeBonusTotal.totalInvest
      } else if (index === 6) { // 节点总业绩,只有白名单可以查看
        return this.nodeBonusTotal.nodeBonus
      } else if (index === 7) { // 已提分红
        return this.nodeBonusTotal.withdrawDivided
      } else if (index === 8) { // 团队总业绩
        return this.teamPerformance
      }
    },
    teamCurrentChange (val) { // val 当前页数，当页数改变时回调
      this.yourTeam.currentPage = val // 修改当前页数
    },
    shareCurrentChange (val) { // val 当前页数，当页数改变时回调
      this.share.currentPage = val // 修改当前页数
    },
    eggCurrentChange (val) { // val 当前页数，当页数改变时回调
      this.eggWinner.currentPage = val // 修改当前页数
    },
    init () {
      /**
       * share部分
       * share.tableData json全部数据接收变量
       * share data数据对象
       * bonusRank 那一页展示的数据
       */
      this.share.tableData = shares
      this.share.total = shares.length
      /**
       * easterWinner部分
       * easterWinnerData json全部数据接收变量
       * eggWinner data数据对象
       * easterWinner 那一页展示的数据
       */
      this.eggWinner.tableData = eggWinners
      this.eggWinner.total = eggWinners.length
    }
  },
  beforeDestroy () {
    // 清空定时器
    clearInterval(this.terminatorBlockNumId)
    clearInterval(this.accountBlockNumId)
    this.terminatorBlockNumId = ''
    this.accountBlockNumId = ''
  }
}
</script>

<style lang="scss" scoped>
.el-pagination {
.el-pager {
  background: #252323;
  background-color: #252323;
  color: #c0c4cc;
  li {
  background: #252323;
  background-color: #252323;
  color: #c0c4cc;
  }
}
}
.box-card {
  .justify-center {
    -webkit-box-pack: center;
    justify-content: center;
  }
  .flex {
    -ms-flex-wrap: wrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
  }
}
</style>
<style scoped>
@import "../assets/table.css";
.el-card {
  margin-top: 1.5rem;
  background-color: #252323;
  border: 0px solid #252323;
  color: #dac28f;
}
.box-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #efdbca, #fdd650);
  font-size: 16px;
  border: 0px solid #f9d76c;
}
.form-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #2c2c2c, #242121);
  color: #f9d76c;
  font-size: 16px;
  border: 0px solid #f9d76c;
}
/* 分页 */
.el-pagination {
  background-color: #252323;
}
.el-pagination >>> button:disabled {
  color: #c0c4cc;
  background-color: #252323;
}
.el-pagination>>>button {
  color: #c0c4cc;
  background-color: #252323;
}
.el-pagination .btn-prev,
.el-pagination .btn-next {
  background-size: 16px;
  background-color: #252323;
  color: #c0c4cc;
}
.el-pager li{
  background: #252323;
  background-color: #252323;
  color: #c0c4cc;
}
.el-pager >>> li {
  background: none;
  background-color: #252323;
  color: #c0c4cc;
}
.sharebonus {
  display: none;
}
.recommend-style {
  color: #dac28f;
  float: right;
}
</style>



